import NavBar from './components/NavBar';
import './App.css';
import { Container } from 'react-bootstrap';
import {Route, Switch} from 'react-router-dom';
import "./api/axiosDefaults";
import SignUpForm from './pages/auth/SignUpPage';
import SignInForm from './pages/auth/SignInPage';
import AddArtworkForm from './pages/artwork/AddArtworkPage';


function App() {

  return (

    <div className="App">
      {/* Add styles main */}
      <NavBar />
      <Container>
        <Switch>
          <Route exact path="/" render={() => <h1>Design Hub</h1>} />
          <Route exact path="/signin" render={() => <SignInForm />} />
          <Route exact path="/signup" render={() => <SignUpForm />} />     
          <Route exact path="/artwork/create" render={() => <AddArtworkForm />} />   
        </Switch>
      </Container>
    </div>

  );
}

export default App;